<!--
 * @Descripttion: 初审台列表
 * @Author: 曾元球
 * @Date: 2019-06-28 14:29:12
 * @LastEditors: 曾元球
 * @LastEditTime: 2019-08-09 16:40:39
 -->
<template>
  <div class="FirstTrial flex-v">
    <el-breadcrumb separator-class="el-icon-arrow-right"
      class="headertitle">
      <el-breadcrumb-item class="titlecolor">初审台</el-breadcrumb-item>
    </el-breadcrumb>
    <div class='query-form'>
      <el-form inline
        :model="queryObj"
        class="default-form-style"
        size="small">
        <el-form-item label="登记日期：">
          <el-date-picker value-format="yyyy-MM-dd"
            v-model="dateRange"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="{ disabledDate: time => time > new Date() }"
            unlink-panels
            clearable></el-date-picker>
        </el-form-item>
        <el-form-item label="体检流水号：">
          <el-input v-model="queryObj.sysGuid"
            placeholder="请输入体检流水号"
            style="width: 160px;"
            clearable></el-input>
        </el-form-item>
        <el-form-item label="姓名：">
          <el-input v-model="queryObj.userName"
            placeholder="请输入姓名"
            clearable></el-input>
        </el-form-item>
        <el-form-item label="体检状态：">
          <el-select v-model="queryObj.examStatus"
            placeholder="全部"
            clearable>
            <el-option label="全部"
              value=''></el-option>
            <el-option label="未初审"
              value="1"></el-option>
            <el-option label="初审完成"
              value="3"></el-option>
            <el-option label="总检失败"
              value="4"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label='机构名称：'>
          <el-select placeholder='全部'
            v-model='queryObj.createOrganizationId'
            clearable>
            <el-option :value='item.sysGuid'
              v-for='item in orgList'
              :key='item.sysGuid'
              :label='item.oname'></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button class="searchbtn"
            icon="el-icon-search"
            round
            @click="onSubmit">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="tabledata table-box">
      <el-table stripe
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        height="100%"
        v-loading="loading"
        header-cell-class-name="custom-table-cell">
        <el-table-column type="index"
          width="50"
          label="序号">
          <template v-slot="{$index}">
            {{$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column label="机构名称"
          prop="createOrganizationName"
          show-overflow-tooltip></el-table-column>
        <el-table-column align="center"
          label="体检流水号"
          show-overflow-tooltip>
          <template v-slot="{row}">
            <a v-if="row.examStatus == 2 || row.examStatus == 3 || row.examStatus == 5"
              class="link"
              @click="gotrialpresonprefected(row)">{{row.sysGuid}}</a>
            <span v-else>{{row.sysGuid}}</span>
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="userName"
          label="姓名"
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column align="center"
          label="性别"
          width="60">
          <template v-slot="{row}">
            {{row.sexType == 1 ? '男' : row.sexType == 2 ? '女' : '不详'}}
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="age"
          label="年龄"
          width="60"></el-table-column>
        <el-table-column align="center"
          label="传输数据"
          show-overflow-tooltip>
          <template v-slot='{row}'>{{row.transfer | emptyData}}</template>
        </el-table-column>
        <el-table-column align="center"
          label="初审医生"
          show-overflow-tooltip>
          <template v-slot='{row}'>{{row.oneAuthUserName | emptyData}}</template>
        </el-table-column>
        <el-table-column align="center"
          label="初审时间"
          width="100">
          <template v-slot='{row}'>{{row.updateDate | formatDate}}</template>
        </el-table-column>
        <el-table-column align="center"
          label="体检状态"
          width="100">
          <template v-slot='{row}'>
            <label v-if="row.examStatus == 1"
              class="daizj">未初审</label>
            <label v-else-if="row.examStatus == 4"
              class="zjloser">总检失败</label>
            <label v-else
              class="cjwc">初审完成</label>
          </template>
        </el-table-column>
        <el-table-column align="center"
          label="操作"
          width="200">
          <template v-slot='{row}'>
            <i class="iconfont bj-await-check"
              v-if="row.examStatus==1"
              @click="audit(row)"></i>
            <i class="icon iconfont bj-edit"
              v-else-if="row.examStatus==4"
              @click="edit(row)"></i>
            <i class="iconfont bj-search"
              v-else
              @click="gotrialpresonprefected(row)"></i>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- 分页 -->
    <div class="footer">
      <bj-pagination :query-obj="queryObj"
        :query-url="queryUrl"
        @listResult="getListResult"
        ref="paginationEvent"
        @controlLoading="controlLoading" />
    </div>
  </div>
</template>


<script>
import { formatDate } from '@/lib/utils'
import { mapState } from 'vuex'
export default {
  name: 'FirstTrial',
  data() {
    let createOrganizationId = this.$store.getters.o_userinfo.orgGuid
    let nowDate = formatDate(new Date())
    return {
      tableData: [],
      dateRange: [nowDate, `${nowDate} 23:59:59`],
      queryUrl: '/user/examUser/getExamUser',
      queryObj: {
        userName: '',
        startTime: nowDate,
        endTime: `${nowDate} 23:59:59`,
        sysGuid: '',
        examStatus: '',
        flow: '1',
        createOrganizationId
      },
      pageNum: 1,
      pageSize: 10,
      loading: false
    }
  },
  created() {},
  methods: {
    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    },

    // 查询
    onSubmit() {
      this.$refs.paginationEvent.initPageList(1)
    },

    controlLoading(loading) {
      this.loading = loading
    },
    edit({ sysGuid }) {
      this.$router.push({
        path: '/trialpresonprefected',
        query: {
          examUserId: sysGuid,
          status: 0 //查看详情标识
        }
      })
    },
    audit({ sysGuid }) {
      this.$router.push({
        path: '/trialpresonprefected',
        query: {
          examUserId: sysGuid,
          status: 0 //查看详情标识
        }
      })
    },
    gotrialpresonprefected({ sysGuid }) {
      this.$router.push({
        path: '/trialpresonprefected',
        query: {
          examUserId: sysGuid,
          status: 1 //查看详情标识
        }
      })
    }
  },
  computed: {
    ...mapState(['orgList'])
  },
  watch: {
    dateRange(val) {
      if (val) {
        this.queryObj.startTime = val[0]
        this.queryObj.endTime = `${val[1]} 23:59:59`
      } else {
        this.queryObj.startTime = ''
        this.queryObj.endTime = ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.FirstTrial {
  height: 100%;
  .zjloser {
    color: #fd8663;
  }
  .tabledata {
    padding: 0 20px;
    overflow: hidden;
  }
  .daizj {
    color: #2ecc71;
  }
  .cjwc {
    color: #ffc000;
  }
  .tabledata {
    flex: 1;
  }
  .footer {
    display: flex;
    height: 60px;
    background: #fff;
    box-shadow: 0px -2px 3px 0px rgba(0, 67, 151, 0.05);
  }
  .yellowcolor {
    color: #ffb915;
  }
  .slansecolor {
    color: #318bfe;
  }
  .sizecolor {
    color: #318bfe;
  }
}
</style>


